.layout {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f5f5f5;
}

/* Header */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #ffffff;
  padding: 16px 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid #e0e0e0;
  flex-shrink: 0;
}

.menu-toggle {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  padding: 8px;
  color: #1976d2;
  display: none;
}

@media (max-width: 768px) {
  .menu-toggle {
    display: block;
  }
}

.header-title {
  font-size: 20px;
  font-weight: bold;
  color: #1976d2;
  flex: 1;
  margin-left: 16px;
}

.user-avatar {
  width: 40px;
  height: 40px;
  background-color: #1976d2;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

/* Main Layout */
.layout {
  display: flex;
  flex-direction: row;
  height: 100vh;
}

.layout > .header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  width: 100%;
}

.layout > .sidebar {
  position: fixed;
  left: 0;
  top: 64px;
  height: calc(100vh - 64px);
  width: 280px;
  background-color: #1e3a5f;
  color: white;
  overflow-y: auto;
  transition: transform 0.3s ease;
  z-index: 50;
}

@media (max-width: 768px) {
  .layout > .sidebar {
    transform: translateX(-100%);
  }

  .layout > .sidebar.open {
    transform: translateX(0);
  }
}

.sidebar-header {
  padding: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  text-align: center;
}

.sidebar-header h2 {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
}

.sidebar-header p {
  margin: 4px 0 0 0;
  font-size: 12px;
  opacity: 0.8;
}

.sidebar-menu {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 16px 8px;
}

.menu-item {
  background: none;
  border: none;
  color: white;
  padding: 12px 16px;
  text-align: left;
  cursor: pointer;
  border-radius: 8px;
  font-size: 14px;
  margin-bottom: 8px;
  transition: background-color 0.2s;
}

.menu-item:hover {
  background-color: rgba(255, 152, 0, 0.2);
}

.menu-item.active {
  background-color: #ff9800;
  color: white;
}

.sidebar-footer {
  padding: 16px 8px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar-footer .menu-item.logout:hover {
  background-color: rgba(244, 67, 54, 0.2);
}

/* Main Content */
.main-content {
  flex: 1;
  margin-top: 64px;
  margin-left: 280px;
  overflow-y: auto;
  padding: 24px;
}

@media (max-width: 768px) {
  .main-content {
    margin-left: 0;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .header {
    padding: 12px 16px;
  }

  .header-title {
    font-size: 18px;
    margin-left: 12px;
  }

  .main-content {
    padding: 16px;
  }
}
